.card_contain {
  height: 450px;
  width: 300px;
  /* border: 1px solid red; */
  /* box-shadow: 1px 5px 0px white; */
  margin: 20px 30px;
  float: left;
  position: relative;
}
.img_contain {
  height: 100%;
  width: 100%;
}
.card_data {
  position: absolute;
  height: 100px;
  width: 100%;
  margin-top: -100px;
  background-color: rgb(58, 57, 57);
  opacity: 0.5;
}
.card_data p {
  font-size: 20px;
  margin: 10px;
  color: rgb(243, 241, 241);
}
.rigth {
  float: right;
  font-size: 15px;
}
.button {
  margin-left: 10px;
  font-size: 10px;
  padding: 5px;
  background-color: rgb(124, 124, 124);
  opacity: 1.5;
  color: white;
  font-weight: bold;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
