.icon_contain{
    height: 100px;
    width: 100px;
    border-radius: 50%;
    background-color: rgb(36, 35, 35);
    margin: 10px;
    border: 0.5px solid grey;
    margin-top: 20px;
    float: left;
    cursor: pointer;
}
.icon_contain:hover{
    background-color: rgb(54, 54, 54);
}
